.main-generator {

  background: $primary-color;

  .gen-jumbotron {
    
    height: 95vh !important;
    position: relative;
    background-color: $primary-color;
    width: 100%;
    height: auto;
    min-height: 95vh;

    &::after {
      position: absolute;
      content: '';
      top: 0;
      left: 0;
      right: 0;
      background-image: url(../media/img/main-waves.png);
      background-size: 100%;
      background-repeat: no-repeat;
      background-position: 100% 85%;
      width: 100%;
      height: 100%;
      opacity: .6;
      z-index: 1;
      @include mq(phone-xl, max) {
        background-position: 100% 0%;
      }
    }

    &-title {
      text-align: center;
    
      .w-100 { filter: drop-shadow(0px 0px 60px rgba(255,255,255,0.075)) }

      h1 { 
        @extend %main-vera;
        @extend %flex-row-between;
        padding: 0;
        @include mq(phone-xl, max) {
          padding: 1rem 0 2.5rem;
          justify-content: space-around;
        }
        span {
          max-width: 100%;
          font-size: 800%;
          @include mq(desk-md, max) {
            font-size: 600%;
          }
          @include mq(phone-xl, max) {
            font-size: 300%;
          }
        }
      }

      h2 {
        color: $white;
        font-size: 4.5rem;
        opacity: 1;
        letter-spacing: 15px;
        @include mq(desk-lg, max) {
          letter-spacing: 5px;
        }
        @include mq(desk-sm, max) {
          letter-spacing: 0px;
        }
        @include mq(tablet-md, max) {
          font-size: 3rem;
        }
        @include mq(phone-xl, max) {
          font-size: 2rem;
          letter-spacing: 0px;
        }
      }

      P {
        margin-top: 1rem;
        color: $white;
        letter-spacing: 2px;
      }
    }

  }

  #gen-voice {
    
    position: relative;

    .border-right-dotted {
      border-right: 2px dashed $white;
      @include mq(phone-xl, max) {
        border: 0px solid transparent
      }
    }

    .d-none-phone {
      @include mq(phone-xl, max) {
        display: none;
      }
    }
    
    .gen {

      &-row {
        display: flex;
        justify-content: center;
        align-items: flex-end;
        padding-top: 1rem;
        &-2 {
          position: relative;
        }
      }
      
      &-row-2-phone {
        .btn-back {
          display: none;
          @include mq(phone-xl, max) {
            display: block;
          }
        }
      }
      
      @include mq(phone-xxl, max) {
        &-row-2-phone {
          min-height: 45vh;
          position: relative;
          .btn-back {
            position: absolute;
            top: 20px;
            left: 20px;
            z-index: 10;
            color: $white;
            text-decoration: none;
          }
          .gen-button {
            display: none;
          }
        }
      }
      
      &-box-container {
        position: relative;
        z-index: 1000;
        background: rgba(#1f114a, .5);
        border-radius: 10px;
      }

      &-message {

        padding: 1rem 1rem 1rem 0;
        @include mq(phone-xl, max) {
          padding: 1rem .5rem 5rem .5rem;
        }

        textarea {
          min-width: 100%;
          max-width: 100%;
          min-height: 25vh;
          max-height: 25vh;
          background: rgba(#2e224f, .75);
          border: 0px solid transparent;
          padding: 1rem;
          border-radius: 10px;
          color: $white;
          font-size: 1.3rem;
          font-family: $futura;
          @include mq(phone-xl, max) {
            min-height: 35vh;
          }
          &:focus {
            outline: 0px;
          }
        }
      }

      &-button {
        
        background-image: url(../media/img/step-1.png);
        background-size: 100%;
        background-repeat: no-repeat;
        background-position: center;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 300px;
        height: 250px;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        overflow: hidden;
        @include mq(phone-xl, max) {
          top: 80%;
          left: 50%;
          transform: translate(-50%, -50%);
          overflow-y: hidden;
          height: 150px;
        }

        a {
          background: transparent;
          color: rgba(#a891b2, .75);
          font-family: $futura;
          font-weight: bold;
          text-decoration: none;
          font-size: 1.1rem;
          margin-bottom: .5rem;
          padding: 20px;
          transition: ease .5s all;
          &:hover {
            color: #a891b2;
          }
        }

      }

      &-time {
        
        display: flex;
        justify-content: flex-end;
        align-items: flex-end;
        border-bottom: 1px solid rgba(#a891b2, .5);
        text-align: right;
        width: 100%;
        height: 50%;

        div {
          p {
            color: rgba(#a891b2, .5);
            margin-bottom: .5rem;
            pointer-events: none;
          }
        }

      }

      &-time_buttons {
        position: relative;
      }

      &-ptd-buttons {
        display: flex;
        justify-content: center;
        align-items: flex-end;
        width: 100%;
        height: 50%;
        padding-bottom: 1.5rem;
        div {
          display: flex;
          align-items: flex-end;
        }
      }
      
      &-ptd-left {
        background: rgba(#2e224f, .9);
        display: flex;
        justify-content: space-around;
        padding: .5rem 1.5rem;
        border-top-left-radius: 50px;
        border-bottom-left-radius: 50px;
        * {
          color: rgba($white,.3);
          margin: 0 12px 0 0;
        }
        .divider-ver {
          height: 20px;
          background: rgba($white, .3);
          border: .0315rem solid rgba($white,.005);
          margin-right: 27px;
        }
      }

      &-ptd-right {
        margin-left: -1.8rem;
        background: $white;
        border-radius: 50%;
        a {
          padding: 12.5px;
          color: $gray-02;
        }
      }

    }

  }

}