.modal {
  
  background-size: 100%;
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  background: rgba(0,0,0,.9);
  z-index: 10000;

  &-danger, &-success {
    &.active {
      display: block;
    }    
  }

  &-box-content {
    background-image: url(../media/img/figurer-bgi-modal.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    @include mq(phone-xl, max) {
      background-image: url(../media/img/figurer-bgi-modal-phone.png);
      background-size: 150% 80%;
    }
    @include mq(phone-sm, max) {
      background-size: 160% 80%;
    }
  }

  &-content {
    text-align: center;
  }

  &-times {
    margin: 0 0 2rem 0;
    @include mq(phone-xl, max) {
      margin-top: -7.5rem;
    }
    a { 
      padding: 1rem ;
      border-radius: 50%;
    }
    a > i, span, svg { color: $white; }
  }

  &-icon {
    margin: 0 0 3rem 0;
    @include mq(phone-xl, max) {
      margin: 6rem 0 1rem 0;
    }
  }

  &-msg {
    margin-bottom: 3rem;
    @include mq(phone-xl, max) {
      margin-bottom: 1rem;
    }
    p { 
      color: $white; 
      font-size: 1rem;
      @include mq(phone-sm, max) {
        font-size: .8rem;
      }
    }
  }

  &-buttons_group {
    a { 
      margin: 0 1rem; 
      @include mq(phone-xl, max) {
        margin: 1rem; 
        font-size: .8rem;
        padding: 5px 15px;
        max-width: 100px;
      }
      @include mq(phone-sm, max) {
        font-size: .7rem;
      }
    }
    .btn.btn-tertiary[id="ok"] {
      min-width: 150px;
    }
  }

}